@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    .__twRange-sm {
        @apply w-full h-1 border-stone-300 bg-stone-50 rounded-lg appearance-none cursor-pointer dark:bg-gray-700
        
    }
    .__twRange-md {
        @apply w-full h-2 border border-stone-300 bg-stone-50 rounded-lg appearance-none cursor-pointer dark:bg-gray-700
    }
    
    ::-webkit-slider-thumb {
        @apply appearance-none bg-cyan-500 w-3 h-3 rounded-full shadow-sm
    }

    ::-moz-range-thumb {
        @apply appearance-none bg-cyan-500 w-3 h-3 rounded-full shadow-sm
    }

}